<template>
  <div class="sections">
    <h1>还款处理</h1>
    <div class="pl_body" style="text-align:center;margin:0 auto;">
        <Form :model="formSel" class="downMenu blackChk" onsubmit="return false;">
            <FormItem>
                <Input v-model="formSel.input" icon="ios-search" @keyup.enter.native="chkClick" placeholder="借款编号/手机号/姓名"></Input>
                <Button id="queryButton" type="success" @click="chkClick">查询</Button>
            </FormItem>
        </Form>
        <div  v-if="vs==true">
            <div class="table_show" :model="loanDetail">
                <table class="headerTb smallTb" border="0" cellpadding="0" cellspacing="0">
                    <tr>
                        <td class="txt_center">借款编号</td><td> {{loanDetail.loanNo}}</td>
                        <td class="txt_center">手机号</td><td>{{loanDetail.custMobile}}</td>
                    </tr>
                    <tr>
                        <td class="txt_center">客户姓名</td><td>{{loanDetail.custName}}</td>
                        <td class="txt_center">产品名称</td><td>{{loanDetail.product}}</td>
                    </tr>
                    <tr>
                        <td class="txt_center">借款金额</td><td>￥{{loanDetail.loanAmount}}</td>
                        <td class="txt_center"></td><td></td>
                    </tr>
                </table>
            </div>

            <div class="table_show" style="margin-top:40px">
                <Row style="margin-bottom:10px"><Col span="12"><h3 class="txt_left">还款计划</h3></Col><Col span="12"><Button class="float_right" type="primary" @click="preRepayShow">提前结清</Button></Col></Row>
                <Table border :columns="columns1" :data="data1"></Table>
            </div>
        </div>
    </div>
    <Modal v-model="model1" title="还款处理" @on-ok="currentRepay">
        <Form :model="formPay" :label-width="90">
            <FormItem label="还款时间">
                <span><DatePicker :value="formPay.time" format="yyyy-MM-dd" placement="bottom-start" placeholder="还款时间" @on-change="dateChange"></DatePicker></span>
            </FormItem>
            <FormItem label="结清金额">
                <Icon type="social-yen"></Icon><Input v-model="formPay.money" class="moneyIpt" placeholder="结清金额"></Input>
            </FormItem>
            <FormItem label="线下还款渠道">
                <Select v-model="formPay.orget" placeholder="请选择">
                    <Option value="001">上海银行</Option>
                    <Option value="002">建设银行</Option>
                  <Option value="003">微信</Option>
                </Select>
            </FormItem>
          <!--<FormItem label="是否结清处理" placeholder="请选择">
              <Select v-model="formPay.orgot">
                  <Option value="0">是</Option>
                  <Option value="1">否</Option>
              </Select>
          </FormItem>-->
        </Form>
    </Modal>

    <Modal v-model="model2" title="提前结清" @on-ok="preRepaySquare">
        <Form :model="formRepay" :label-width="90" >
            <FormItem label="结清时间">
                <span><DatePicker :value="formRepay.time" format="yyyy-MM-dd" placement="bottom-start" placeholder="还款时间" @on-change="datePreChange"></DatePicker></span>
            </FormItem>
            <FormItem label="结清金额">
                <Icon type="social-yen"></Icon><!--<span style="padding-left:26px;" v-text="formRepay.money"></span>--><Input v-model="formRepay.money" class="moneyIpt" placeholder="结清金额"></Input>
            </FormItem>
            <FormItem label="线下还款渠道">
              <Select v-model="formRepay.orget" placeholder="请选择">
                <Option value="001">上海银行</Option>
                <Option value="002">建设银行</Option>
                <Option value="003">微信</Option>
              </Select>
            </FormItem>
            <!--<FormItem label="减免金额">
                <Icon type="social-yen"></Icon><Input v-model="formRepay.moneyLose" class="moneyIpt" placeholder="减免金额"></Input>
            </FormItem>-->
            <!--<FormItem label="是否到账">
                <Select v-model="formRepay.orget" placeholder="请选择">
                    <Option value="0">已到账</Option>
                    <Option value="1">未到账扣款</Option>
                </Select>
            </FormItem>-->
        </Form>
    </Modal>
    </div>
  </div>
</template>

<script>

export default {
  name: 'sections',
  data () {
    return {
        model1:false,
        model2:false,
        formSel:{
            input:''
        },
        vs:false,
        operatorPersonId:0,
        totalMoney:0,
        prePlanStage:"",
        planStatus:"",
        channelCode:"",
        acctId:"",
        flowNo:"",
        formPay:{
            time:[],
            money:'300.00',
            orget:'0',
            orgot:'0'
        },
        loanDetail:{},
        formRepay:{
            time:[],
            money:'300.00',
            moneyLose:'',
            orget:'0'
        },
        columns1:[{
            title:'期数',
            width:80,
            key:'planStage'
        },{
            title:'应还时间',
            key:'planLtm',
            width:160
        },{
            title:'应还总额',
            width:100,
            key:'planTotal'
        },{
            title:'应还本金',
             width:100,
            key:'planPrp'
        },{
            title:'应还利息',
             width:100,
            key:'planInt'
        },{
            title:'应还服务费',
             width:100,
            key:'planSfe'
        },{
            title:'应还罚息',
             width:100,
            key:'planPin'
        },{
            title:'应还违约金',
             width:100,
            key:'dfltAmount'
        },{
            title:'实还时间',
            key:'realTime',
            width:160
        },{
            title:'实还总额',
             width:100,
            key:'repAmount'
        },{
            title:'实还本金',
             width:100,
            key:'realPrp'
        },{
            title:'实还利息',
             width:100,
            key:'realInt'
        },{
            title:'实还服务费',
             width:100,
            key:'realSfe'
        },{
            title:'实还罚息',
             width:100,
            key:'realPin'
        },{
            title:'实还违约金',
             width:100,
            key:'dfltAmount'
        },{
            title:'状态',
             width:100,
            key:'status'
        },{
            title:'操作',
            key:'action',
            width:100,
            fixed:'right',
            align:'center',
            render:(h,params)=>{
                return h('div',[
                        h('Button',{
                            props:{
                                type:'primary',
                                size:'small'
                            },
                            on:{
                                click:()=>{
                                    this.show(params.index);
                                }
                            }
                        },'还款处理')
                    ])
            }
        }],
        data1:[/*{
            dates:1,
            repayTm:'2017-08-08',
            repayCountMn:'500.00',
            repayMn:'500.00',
            repayDs:'500.00',
            repaySv:'500.00',
            repayLt:'500.00',
            repayDis:'500.00',
            repayTmRl:'500.00',
            repayMnCountRl:'500.00',
            repayMnRl:'500.00',
            repayDsRl:'500.00',
            repaySvRl:'500.00',
            repayLtRl:'500.00',
            repayDisRl:'500.00',
            repayStatus:'已还款'
        }*/]
	    }
	},
    computed :{
        pageWin(){
            return Math.max(window.innerWidth,document.documentElement.clientWidth,document.body.clientWidth);
        }
    },

	methods: {
        chkClick(){
            this.findPlan();
            this.vs=true;
        },
        dateChange(date){
            this.formPay.time=date;
        },
      datePreChange(date){
        this.formRepay.time=date;
      },
        show(index){
          if(this.planStatus == 4){
              alert("有还款正在处理中,不能还款");
              return;
          }
          var planStage = this.data1[index].planStage;
          if(planStage != this.prePlanStage){
              alert("请选择正确的还款期数,不能还本期");
              return;
          }
          var myDate = new Date();
          this.formPay.time = myDate.getFullYear()+"-"+(myDate.getMonth()+1)+"-"+myDate.getDate();
          this.model1=true;
        },
        findPlan(){
            var that=this;
            that.operatorPersonId=0;
            that.totalMoney=0;
            that.prePlanStage="";
            that.planStatus="";
            that.channelCode="";
            that.acctId="";
            that.flowNo="";
            var detail = {"value":that.formSel.input};
          //http://localhost:8081/cm-loan-finance  ,'Access-Control-Allow-Origin':'*'   withCredentials: true,
           this.axios
             .post('/detail/findRepayDetailFromCore',JSON.stringify(detail),
                      {headers:{'Content-Type':'application/json'}
                  })
             .then(function(respon){
                    if(respon.data.resCode == 1){
                      var amountTotal = 0;
                      respon.data.result.repayDetail.forEach(function(element) {
                            var amount = 0;
                            if(element.status ==0){
                                element.status = "计划未开始";
                                //amount = element.planPrp+element.dfltAmount - element.repAmount;
                            }else if(element.status ==1){
                              if(that.prePlanStage == ''){
                                that.prePlanStage = element.planStage;
                              }
                                element.status = "未到期";
                                amount = element.planPrp+element.dfltAmount - element.repAmount;
                            }else if(element.status ==2){
                              if(that.prePlanStage == ''){
                                that.prePlanStage = element.planStage;
                              }
                                element.status = "本期待还";
                                amount = element.planPrp+element.planInt+element.planPin+element.planSfe-element.repAmount;
                            }else if(element.status ==3){
                              if(that.prePlanStage == ''){
                                that.prePlanStage = element.planStage;
                              }
                                element.status = "逾期待还";
                                amount = element.planPrp+element.planInt+element.planPin+element.planSfe-element.repAmount;
                            }else if(element.status ==4){
                                that.planStatus = 4;
                                element.status = "还款中";
                                amount = element.planPrp+element.planInt+element.planPin+element.planSfe-element.repAmount;
                            }else if(element.status ==5){
                                element.status = "已还清";
                            }else if(element.status ==6){
                                element.status = "合同取消";
                            }
                            amountTotal += amount;

                        }, this);

                        that.totalMoney = amountTotal;
                        that.data1 = respon.data.result.repayDetail;
                        that.loanDetail = respon.data.result.loanDetail;
                        that.acctId = respon.data.result.loanDetail.acctId;
                        that.channelCode = respon.data.result.loanDetail.channelCode;
                        that.flowNo = respon.data.result.loanDetail.flowNo;
                    }
                 })
             .catch(function(resp){
               this.$Message.warning('系统异常');
             });

        },
    check(){
      if(this.custId == ""){
        alert("数据有误,请联系我们");
        return false;
      }
      if(this.prePlanStage == ""){
        alert("期数数据有误,请联系我们");
        return false;
      }
      if(this.formPay.orget == "0"){
        alert("请选择线下还款渠道");
        return false;
        //this.$Message.warning('请选择线下还款渠道');
      }
    },
      currentRepay(){
          //var channel = "";
          //var flowNo = "";
          if(this.custId == ""){
              alert("数据有误,请联系我们");
              return false;
          }
          if(this.prePlanStage == ""){
              alert("期数数据有误,请联系我们");
              return false;
          }
          if(this.formPay.orget == "0"){
            alert("请选择线下还款渠道");
            return false;
            //this.$Message.warning('请选择线下还款渠道');
          }
          var acctId = this.acctId;
          var num = new Number(this.formPay.money*100);
          var amount = num.toFixed(0);
          var planStage = this.prePlanStage;
          var date = this.formPay.time;
          var operatorPersonId = this.operatorPersonId;
          var flowNo = this.flowNo;
          var detail = {};
          detail['acctId']=acctId;
          detail['amount']=amount;
          detail['planStage']=planStage;
          detail['repayDate']=date;
          detail['operator']=operatorPersonId;
          detail['flowNo']=flowNo;
          detail['platNo']=this.formPay.orget;
          detail['channel']=this.channelCode;
          detail['type']=0;
          detail['remark']="";
          //this.$emit('chkClick');
          //this.chkClick();
          //$("#queryButton").click();
          this.axios.post('/detail/offlineRepay',JSON.stringify(detail),
            {headers:{'Content-Type':'application/json'}
            }).then(function(respon){
            if(respon.data.resCode == 1){
              alert("线下还款成功");
              //window.location.href = "/repayment";
              //this.chkClick();
              $("#queryButton").click();
              this.model1=false;
            }else{
              alert("线下还款失败,失败原因是:"+respon.data.message);
              this.model1=false;
            }
          }).catch(function(resp){
            this.$Message.warning('系统异常');
          });
      },
      preRepayShow(){
        var that = this;
        if(this.planStatus == 4){
          alert("有还款正在处理中,不能还款");
          return false;
        }
        if(that.totalMoney == 1){
            alert("数据有问题,请联系我们");
            return false;
        }
        this.formRepay.money=that.totalMoney;
        var myDate = new Date();
        this.formRepay.time = myDate.getFullYear()+"-"+(myDate.getMonth()+1)+"-"+myDate.getDate();
        this.model2=true;

      },

      preRepaySquare(){
        if(this.custId == ""){
          alert("数据有误,请联系我们");
          return false;
        }
        if(this.prePlanStage == ""){
          alert("期数数据有误,请联系我们");
          return false;
        }
        if(this.formRepay.orget == "0"){
          alert("请选择线下还款渠道");
          return false;
        }
        var acctId = this.acctId;
        //var num = new Number(this.totalMoney*100);
        var num = new Number(this.formRepay.money*100);
        var amount = num.toFixed(0);
        var planStage = this.prePlanStage;
        var date = this.formRepay.time;
        var operatorPersonId = this.operatorPersonId;
        var flowNo = this.flowNo;
        var detail = {};
        detail['acctId']=acctId;
        detail['amount']=amount;
        detail['planStage']=planStage;
        detail['repayDate']=date;
        detail['operator']=operatorPersonId;
        detail['flowNo']=flowNo;
        detail['platNo']=this.formRepay.orget;
        detail['channel']=this.channelCode;
        detail['type']=0;
        detail['remark']="";
        this.axios.post('/detail/offlinePreRepay',JSON.stringify(detail),
          {headers:{'Content-Type':'application/json'}
          }).then(function(respon){
          if(respon.data.resCode == 1){
            alert("线下提前还清成功");
            //window.location.href = "/repayment";
            //this.chkClick();
            $("#queryButton").click();
            this.model1=false;
          }else{
            alert("线下提前还清失败,失败原因是:"+respon.data.message);
            this.model1=false;
          }
        }).catch(function(resp){
          this.$Message.warning('系统异常');
        });
      }

    }
}
</script>




